.skills-card {
    width: 300px;
    height: 400px;
    perspective: 1000px;
    margin: 20px auto;
  }
  
  .skills-card-inner {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: transform 0.5s;
  }
  
  .skills-card.flipped .skills-card-inner {
    transform: rotateY(180deg);
  }
  
  .skills-card-front,
  .skills-card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }
  
  .skills-card-front {
    background-color: #f5f5f5;
    color: #333;
  }
  
  .skills-card-back {
    background-color: #007bff;
    color: #fff;
    transform: rotateY(180deg);
  }
  
  .skills-card button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 14px;
  }
  
  .skills-details {
    text-align: left;
  }
  
  .skills-details h3 {
    font-size: 18px;
    margin-bottom: 10px;
    color: #fff;
  }
  
  .skills-details ul {
    list-style-type: disc;
    margin-left: 20px;
    color: #fff;
  }
  
  .skills-details li {
    font-size: 14px;
    margin-bottom: 5px;
  }
  
 