/* Footer.css */
.footer {
    background-color: #f5f5f5;
    padding: 20px 0;
    text-align: center;
  }
  
  .contact-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .contact-item {
    margin: 10px;
    display: flex;
    align-items: center;
  }
  
  .contact-item .icon {
    margin-right: 5px;
  }
  
  
  